<template>
<li>
    <div class="post">
    <div class="post-body">
      <!-- 1 -->
      <a href="#" :title="item.title">
      <div class="post-main">
        <figure>
          <!-- 2 -->
          <img :src="item.thumb" alt=""/>
        </figure>
        <div class="post-title">
          <!-- 3 -->
          {{ item.title }}
        </div>
      </div>
    </a>
    <div class="cardinfo">
      <div class="author">
        <a href="#">
          <img src="../assets/uploads/avatar.jpg" alt="" />
          <span>镇长大人</span>
        </a>
      </div>
      <div class="time">
        <!-- 4 -->
        <span>{{ publishDate | formatDate }}</span>
      </div>
    </div>
    <div class="views">
      <!-- 5 -->
      <span class="views-left"> <i class="el-icon-view"></i>&nbsp;{{ item.views }} </span>
      <!-- 6 -->
      <span class="views-right"> <i class="el-icon-star-on"></i>&nbsp;{{ item.favorites }}</span>
    </div>
    </div>
  </div>
</li>
</template>

<script>
export default {
  name: 'HomePost',
  props: {
    item: {
      type: Object
    }
  },
  data () {
    return {
      publishDate: new Date('2024-08-15'),
      isOut: false
    }
  },
  // Vue 实例中定义过滤器
  filters: {
    formatDate (date) {
      if (!date) return ''
      const currentDate = new Date()
      const targetDate = new Date(date)
      const timeDiff = Math.abs(currentDate - targetDate)
      const daysDiff = Math.floor(timeDiff / (1000 * 60 * 60 * 24))
      if (daysDiff > 7) {
        return `${Math.floor(daysDiff / 7)}周前`
      } else if (daysDiff > 0) {
        return `${daysDiff}天前`
      } else {
        const hoursDiff = Math.floor(timeDiff / (1000 * 60 * 60))
        if (hoursDiff > 0) {
          return `${hoursDiff}小时前`
        } else {
          return '刚刚'
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
  a {
    color: #222;
    text-decoration: none;
    outline: none;
  }
  li {
    display: block;
    float: left;
    width: 20%;
    padding: 6px;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, 0.1);
    &:hover {
      .post {
        -webkit-transform: translate(0, -3px);
        transform: translate(0, -3px);
        box-shadow: 0 1px 6px 1px rgba(0, 0, 0, 0.25);
        transition: all 125ms cubic-bezier(0.02, 0.01, 0.47, 1);
      }
    }
  }
  .post {
    .post-body {
      border-radius: 4px;
      overflow: hidden;
      background: #fff;
      a {
        display: block;
        .post-main {
          display: inline-block;
          position: relative;
          width: 100%;
          padding-top: 150%;
          overflow: hidden;
          figure {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            img {
              width: 100%;
              height: 100% !important;
              object-fit: cover;
              border: 0;
              vertical-align: middle;
            }
          }
          .post-title {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: auto;
            line-height: 18px;
            padding: 3px 1px;
            text-align: center;
            overflow: hidden;
            -moz-box-orient: vertical;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            color: #ffffff;
            font-size: 12px;
            text-shadow: #626675 1px 1px 2px;
            background-image: linear-gradient(
              180deg,
              rgba(0, 0, 0, 0) 0%,
              rgba(0, 0, 0, 0.8) 70%
            );
          }
        }
        .post-main::before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          width: 100%;
          height: 48px;
          background-image: url("../assets/images/post-bg.png");
          background-repeat: repeat-x;
          border-radius: 2px;
        }
      }
      .cardinfo {
        height: 30px;
        line-height: 30px;
        padding: 0 10px 0 8px;
        color: #888686;
        font-size: 12px;
        .author {
          float: left;
          height: 30px;
          a {
            float: left;
            display: block;
            height: 26px;
            text-overflow: ellipsis;
            -o-text-overflow: ellipsis;
            white-space: nowrap;
            word-wrap: normal;
            padding-top: 5px;
            padding-left: 0;
            padding: 3px 5px;
            overflow: hidden;
            img {
              float: left;
              height: 100%;
              width: auto;
              border-radius: 50%;
              box-shadow: none;
            }
            span {
              padding-left: 5px;
              font-size: 12px;
              line-height: 20px;
              float: left;
              overflow: hidden;
              text-overflow: ellipsis;
              -o-text-overflow: ellipsis;
              white-space: nowrap;
              word-wrap: normal;
              width: 55px;
              color: #626675;
              font-family: Arial, sans-serif;
            }
          }
        }
        .time {
          float: right;
          position: relative;
        }
      }
      .views {
        padding: 0 10px;
        margin-bottom: 5px;
        height: 20px;
        span {
          background: #8787879e;
          border-radius: 4px;
          padding: 0 5px;
          font-size: 12px;
          color: #fff;
        }
        .views-left {
          float: left;
        }
        .views-right {
          float: right;
        }
      }
    }
  }
  @media screen and (max-width: 800px) {
    li {
      width: 50%;
    }
  }
</style>
