  },
  async created () {
    const res = await gethomeData()
    this.List = res
  },
  methods: {
    textoneover () {
      this.isoneOver = true
      this.istwoOver = false
      this.isthreeOver = false
    },
    texttwoover () {
      this.isoneOver = false
      this.istwoOver = true
      this.isthreeOver = false
